import React, { Component } from 'react'
import UserSearchBox from '@/components/SearchBox/UserSearchBox'
import './Participants.scss'

const addParticipants = (map, users, addition) => {
  users.forEach(user => {
    map.set(user._id, {
      ...(map.get(user._id) || user),
      ...addition,
    })
  })
}

export default class Participants extends Component {
  static defaultProps = {
    className: '',
    editors: [],
    owners: [],
    players: [],
  }

  static getDerivedStateFromProps(props, state) {
    const { editors, owners, players } = props
    const map = new Map()
    addParticipants(map, editors, { isEditor: true })
    addParticipants(map, owners, { isOwner: true })
    addParticipants(map, players, { isPlayer: true })

    return {
      ...state,
      participants: Array.from(map.values()),
    }
  }

  render = () => {
    const { className } = this.props
    const { participants } = this.state

    return (
      <div className={`participants ${className}`}>
        <ul>
          {this.participants.map(user => (
            <li key={user._id}>{user.username}</li>
          ))}
        </ul>
        <UserSearchBox />
      </div>
    )
  }
}
